"use client";
import { useRouter } from "next/navigation";
import { useState, FormEvent, useEffect } from "react";
import { useContext } from "react";
import { LoginRegistrationContext } from "@/app/context/login_registration";
const Login = () => {
  const router = useRouter();
  const [email, setEmail] = useState<string>("");
  const [password, setPassword] = useState<string>("");
  const [message, setMessage] = useState<string | null>(null);
  const [responseMessage, setResponseMessage] = useState<string>("");
  const [errs, setErrs] = useState<
    { Errors?: { email?: string; password?: string } } | string | null
  >(null);

  const API_URL = process.env.NEXT_PUBLIC_API_URL;
  const context = useContext(LoginRegistrationContext);
  const submitForm = async (e: FormEvent) => {
    e.preventDefault();
    setResponseMessage("");
    const data = { email, password };

    try {
      const res = await fetch(`${API_URL}/login`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify(data),
      });

      if (!res.ok) {
        const errorData = await res.json();
        console.log(errorData);
        setErrs(errorData);
      } else {
        const result = await res.json();

        setResponseMessage(result);
        if (result === "Úspešene ste sa prihlásili") {
          context?.setResponse((prev) => prev + 1);
        }
      }
    } catch (error: any) {
      console.error("Chyba pri prihlasovaní:", error.message);
    }
  };

  return (
    <section className="flex-center h-[90vh]">
      <form onSubmit={submitForm} className="form ">
        <h2 className="text-black text-center m-5">Prihlásenie</h2>
        {responseMessage && responseMessage !== "Úspešene ste sa prihlásili" ? (
          <p className="text-red-500 p-5 text-center border-red-500 border-2">
            {responseMessage}
          </p>
        ) : null}
        {responseMessage && responseMessage === "Úspešene ste sa prihlásili" ? (
          <p className="text-purple p-5 text-center border-purple border-2">
            {responseMessage}
          </p>
        ) : null}

        <div className="w-full m-2 h-fit flex flex-col">
          <label className="mb-4">Email:</label>
          <input
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="w-full"
          />
        </div>

        <div className="w-full m-2 h-fit flex flex-col">
          <label className="">Heslo:</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="w-full"
          />
        </div>

        <button
          type="submit"
          className="buttonForm bg-blue-600 hover:bg-blue-800"
        >
          Prihlásiť sa
        </button>
      </form>
    </section>
  );
};

export default Login;
